.button {
    padding: 14px 18px;
    border-radius: 10px;
    border: 2px solid var(--hint);
    background-color: transparent;
    font-size: 18px;
    font-weight: 700;
    cursor: pointer;
    color: var(--gray);
    transition: .2s;
}
.button:hover {
    border-color: var(--gray);
}
